import React, { useState, useEffect } from "react";
import {
    MapContainer,
    TileLayer,
    Marker,
    Popup,
    Tooltip,
    Circle,
    CircleMarker,
} from "react-leaflet";
import sampleData from "../../data/data.json";
import sampleData2 from "../../data/data2.json";

import { L } from "leaflet";
import { faBicycle } from "@fortawesome/free-solid-svg-icons";

const fillBlueOptions = { fillColor: "blue" };
const blackOptions = { color: "black" };
const purpleOptions = { color: "purple" };
const limeOptions = { color: "lime", fillColor: "rgb(100,100,100,0.8)" };
const redOptions = { color: "red" };

const departure = sampleData.departure.data[0];
const departure_group = sampleData.departure_group.data;
const destination = sampleData.destination.data[0];
const destination_group = sampleData.destination_group.data;

// const myIcon = new L.Icon({
//     iconUrl:
//         'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="' +
//         faBicycle.icon[4] +
//         '"/></svg>',
//     // iconRetinaUrl: "marker",
//     popupAnchor: [-0, -0],
//     iconSize: [32, 45],
// });

const MyMap = () => {
    const [position, setPosition] = useState([37.5, 127.0]);

    useEffect(() => {
        setPosition(departure[""], departure[""]);
    }, []);

    return (
        <div id="map">
            <MapContainer
                style={{ height: "100vh" }}
                center={position}
                zoom={13}
            >
                <TileLayer
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="http://{s}.tile.osm.org/{z}/{x}/{y}.png"
                />

                <Marker
                    position={[departure[""], departure[""]]}
                    // icon={myCustomIcon}
                >
                    <Popup>
                        <span>
                            (): {departure["()"]}
                            <br />
                            : {departure[""]}
                            <br />
                             : {departure[""]}
                        </span>
                    </Popup>
                    <Tooltip sticky> - </Tooltip>
                </Marker>
                <Circle
                    center={[departure[""], departure[""]]}
                    pathOptions={limeOptions}
                    radius={5000}
                />
                <Marker position={[destination[""], destination[""]]}>
                    <Popup>
                        <span>
                             : {destination["()"]}
                            <br />
                             : {destination[""]}
                            <br />
                             : {destination[""]}
                        </span>
                    </Popup>
                    <Tooltip sticky> - </Tooltip>
                </Marker>
                <Circle
                    center={[destination[""], destination[""]]}
                    pathOptions={purpleOptions}
                    radius={5000}
                />
                {departure_group.map((station) => (
                    <CircleMarker
                        center={[station[""], station[""]]}
                        pathOptions={{
                            fillColor: `rgb(100, 100, 255, ${
                                station[""] / 10
                            } )`,
                        }}
                        radius={80 * (station[""] / 100)}
                    >
                        <Popup>
                            <span>
                                (): {station["()"]}
                                <br />
                                : {station[""]}
                                <br />
                                 : {station[""]}
                            </span>
                        </Popup>
                        <Tooltip sticky>{station[""]}</Tooltip>
                    </CircleMarker>
                ))}
                {destination_group.map((station) => (
                    <CircleMarker
                        center={[station[""], station[""]]}
                        pathOptions={redOptions}
                        radius={80 * (station[""] / 100)}
                    >
                        <Popup>
                            <span>
                                (): {station["()"]}
                                <br />
                                : {station[""]}
                                <br />
                                 : {station[""]}
                            </span>
                        </Popup>
                        <Tooltip sticky>{station[""]}</Tooltip>
                    </CircleMarker>
                ))}
            </MapContainer>
        </div>
    );
};

export default MyMap;
